body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 90%;
  max-width: 700px;
  margin: 10px auto;
  text-align: center;
  border: 1px solid black;
  border-radius: 12px 12px 0px 0px;
  background-color: rgb(12, 99, 180);
}
h1 {
  font-size: 29px;
  color: white;
  margin-bottom: 12px;
}
h3 {
  font-size: 20px;
  padding-top: 0px;
  margin-top: 0px;
  color: white;
}
span {
  color: white;
}
nav {
   border-radius: 12px 12px 0px 0px;
  padding: 6px;
  background-color: #5f1c9c;
}
h2 {
  color: deeppink;
  padding: 0px 14px;
  margin-bottom: 0px;
}
p {
  padding: 0px 14px;
  color: whitesmoke;
}

.book-box {
  border-radius: 12px;
  padding: 2px;
  width: 88%;
  margin: 10px auto;
  background-color: #5f1c9c;
  text-align: left;
  padding-left: 2px;
}

input[type="text"] {
  display: block;
  padding: 8px;
  width: 65%;
  margin: 4px auto;
}

input[type="submit"] {
  padding: 14px;
  margin: 10px auto;
  border-radius: 20px;
  background-color: #5f1c9c;
  color: white;
  border-style: none;
  font-size: 18px;
}
.book-box:hover {
  opacity: 0.8;
}
.notice{
font-size: 24px;
font-weight: bold;
color:black;
}